html {
  &.home {
    body {
      height: 100vh;
      width: 100vw;
      position: relative;
      overflow: hidden;

      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;

      .monogram {
        @include monogram(30vh, 40vh, 0.5);
      }

      .icons {
        @include icons(white-on-black);
      }
    }
  }
}
