/*
Settings
================================================================================
*/
@import url(http://fonts.googleapis.com/css?family=Montserrat:400,700,);
@import url(http://fonts.googleapis.com/css?family=Lekton:700,);
.white {
  background: #fff; }

.black {
  background: #000; }

.white-on-black {
  color: #fff;
  background-color: #000; }

/*
  (TEXT) PARAMS
  =================
  1. Namespace -- (class name) where glitch is applied
  2. Intensity -- (integer) how intensive the glitching is
  3. Text color
  4. Background color (flat)
  5. Highlight #1 color -- (color) color1
  6. Highlight #2 color -- (color) color2
  7. Width (px) -- width of the glitch
  8. Height (px) -- height of the glitch
*/
.montserrat-normal {
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  font-style: normal; }

.montserrat-bold {
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  font-style: bold; }

.lekton-bold {
  font-family: 'Lekton', sans-serif;
  font-weight: 700;
  font-style: bold; }

.scale--default {
  font-size: 100%;
  line-height: 1.48; }

.text-style--default {
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 100%;
  line-height: 1.48;
  color: #fff;
  background-color: #000; }

.text-style--title {
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  font-style: bold;
  text-transform: uppercase;
  letter-spacing: 1.1px; }

.text-style--subtitle {
  font-family: 'Lekton', sans-serif;
  font-weight: 700;
  font-style: bold; }

/*
Elements on the page
================================================================================
*/
/*
  Normalize.css // Skip this part
  ------------------------------------------------------------------------------
  */
html {
  font-family: sans-serif;
  /* 1 */
  -ms-text-size-adjust: 100%;
  /* 2 */
  -webkit-text-size-adjust: 100%;
  /* 2 */ }

body {
  margin: 0; }

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section,
summary {
  display: block; }

audio,
canvas,
progress,
video {
  display: inline-block;
  /* 1 */
  vertical-align: baseline;
  /* 2 */ }

audio:not([controls]) {
  display: none;
  height: 0; }

[hidden],
template {
  display: none; }

a {
  background-color: transparent; }

a:active,
a:hover {
  outline: 0; }

abbr[title] {
  border-bottom: 1px dotted; }

/**
   * Address style set to `bolder` in Firefox 4+, Safari, and Chrome.
   */
b,
strong {
  font-weight: bold; }

dfn {
  font-style: italic; }

h1 {
  font-size: 2em;
  margin: 0.67em 0; }

mark {
  background: #ff0;
  color: #000; }

small {
  font-size: 80%; }

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sup {
  top: -0.5em; }

sub {
  bottom: -0.25em; }

img {
  border: 0; }

svg:not(:root) {
  overflow: hidden; }

figure {
  margin: 1em 40px; }

hr {
  -moz-box-sizing: content-box;
  box-sizing: content-box;
  height: 0; }

pre {
  overflow: auto; }

code,
kbd,
pre,
samp {
  font-family: monospace, monospace;
  font-size: 1em; }

button,
input,
optgroup,
select,
textarea {
  color: inherit;
  /* 1 */
  font: inherit;
  /* 2 */
  margin: 0;
  /* 3 */ }

button {
  overflow: visible; }

button,
select {
  text-transform: none; }

button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: button;
  /* 2 */
  cursor: pointer;
  /* 3 */ }

button[disabled],
html input[disabled] {
  cursor: default; }

button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0; }

input {
  line-height: normal; }

input[type="checkbox"],
input[type="radio"] {
  box-sizing: border-box;
  /* 1 */
  padding: 0;
  /* 2 */ }

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  height: auto; }

input[type="search"] {
  -webkit-appearance: textfield;
  /* 1 */
  -moz-box-sizing: content-box;
  -webkit-box-sizing: content-box;
  /* 2 */
  box-sizing: content-box; }

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none; }

fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em; }

legend {
  border: 0;
  /* 1 */
  padding: 0;
  /* 2 */ }

textarea {
  overflow: auto; }

optgroup {
  font-weight: bold; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

td,
th {
  padding: 0; }

/*
  Normalize.css // End
  ------------------------------------------------------------------------------
  */
